  margin-bottom: -1%;
}
.c-svg__background--bottom {
  fill: #BDd3DD;
  transform: scaleY(-1);
  margin-top: -1%;
}
.c-svg__background--bottom--front {
  fill: none;
  margin-top: -5%;
