.p-header__present {
  position: absolute;
  top: 51%;
  left: 7%;
  z-index: 99;
  animation-name: upDown;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: forwards;
}
@media screen and (min-width: 1025px) {
  .p-header__present {
    top: 26%;
    left: 18%;
  }
}
.p-header__nav {
  display: none;
  position: absolute;
  top: 0;
  right: -100%;
  z-index: 103;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}
.p-header__nav::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-color: #BDd3DD;
  width: 50%;
  height: 100%;
}
@media screen and (min-width: 1025px) {
