}
.p-header__open--btn {
  position: fixed;
  top: 6.5%;
  right: 10%;
  z-index: 104;
}
@media screen and (min-width: 600px) {
  .p-header__open--btn {
    top: 3.5%;
    right: 5%;
  }
}
@media screen and (min-width: 1025px) {
  .p-header__open--btn {
    display: none;
  }
}

@keyframes upDown {
  0% {
    transform: translate(0);
  }
  12% {
    transform: translateY(10px);
  }
  24% {
    transform: translateY(20px);
  }
  36% {
    transform: translateY(10px);
  }
  50% {
    transform: translateY(0px);
  }
  62% {
    transform: translateY(-10px);
  }
  74% {
    transform: translateY(-20px);
  }
  86% {
    transform: translateY(-10px);
  }
  100% {
    transform: translate(0);
  }
}
